.content {
  align-self: stretch;
  background: radial-gradient(
    50% 50%at 50% 50%,
    rgba(107, 107, 107, 0.36),
    rgba(107, 107, 107, 0.57) 50%,
    rgba(107, 107, 107, 0)
  );
  height: 586px;
  overflow-y: auto;
  flex-shrink: 0;
  flex-direction: column;
  padding: 0 0.9718993902206421px 0 0;
  box-sizing: border-box;
  justify-content: flex-start;
  gap: 27px;
  text-align: left;
  font-size: var(--font-size-21xl);
  color: var(--color-white);
  font-family: var(--font-carme);
}
.button1,
.content,
.frame {
  display: flex;
  align-items: center;
}
.frame {
  align-self: stretch;
  overflow: hidden;
  flex-direction: column;
  justify-content: flex-start;
}
.button1 {
  position: relative;
  letter-spacing: 0.05em;
  justify-content: center;
  width: 50px;
  height: 64px;
  flex-shrink: 0;
  cursor: pointer;
}
.button1:hover {
  color: var(--color-white);
}
.button,
.footer,
.portfolioPage {
  display: flex;
  flex-direction: column;
}
.button {
  width: 52px;
  align-items: flex-start;
  justify-content: center;
}
.footer,
.portfolioPage {
  align-items: center;
}
.footer {
  align-self: stretch;
  background: linear-gradient(180deg, rgba(58, 58, 58, 0), #232323 9.38%);
  height: 50px;
  justify-content: flex-end;
  text-align: center;
  font-size: var(--font-size-2xl);
  color: var(--color-gray);
  font-family: var(--font-carme);
}
.portfolioPage {
  position: relative;
  background-color: var(--color-darkslategray-100);
  width: 100%;
  height: 800px;
  overflow: hidden;
  justify-content: flex-start;
  gap: 5px;
}
@media screen and (max-width: 420px) {
  .experiences {
    font-size: var(--font-size-9xl);
  }
  .gallery {
    flex-direction: column;
  }
}
@media screen and (max-width: 350px) {
  .thomsonReuters1 {
    font-size: 15px;
  }
  .frame {
    display: flex;
  }
}
@media screen and (max-width: 550px) {
  .codingMindAcademy,
  .thomsonReuters1 {
    font-size: var(--font-size-3xl);
  }
  .thomsonReuters {
    height: 217px;
  }
}
